

.search_back_color {
    align-items: center;
    text-align: center;
    overflow: hidden;
    background-color: #282828;
    height: 100vh;
}

.user_search_random {
    width: 1050px;
    text-align: center;
    display: grid;
    grid-template-columns: 1fr 120px; /* Add this line to create two columns */
    grid-gap: 10px; /* Add some gap between the columns */
    padding-top: 250px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 100px;
  }
  
  .user_search_box {
    position: relative;
    display: flex;
    background-color: #282828;
    width: 100%;
    height: 110px;
  }
  
.user_Searching {
    text-decoration: none;
    position: absolute;
    background-color: #282828;
    width: 84.6%;
    height: 105px;
    padding-left: 25px;
    padding-right: 100px;
    border: solid 5px white;
    border-radius: 6px;
    color: white;
    font-size: 45px;
    font-weight: 700;
}
  
@media (max-width: 1200px) {
    .user_search_random {
      width: 90%;
    }
  
    .user_search_box {
      width: 85%;
    }
  
    .user_Searching {
      width: 90%;
      padding-right: 80px;
    }
}
  
@media (max-width: 600px) {
    .user_search_random {
      flex-direction: column;
      align-items: center;
    }
  
    .user_search_box {
      width: 95%;
    }
  
    .user_Searching {
      width: 100%;
      padding-right: 50px;
    }
}

.find_user {
    background-image: url("./search_lens.png");
    background-size: cover;
    position: relative;
    background-color: #282828;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: 3%;
    width: 60px;
    height: 60px;

    border: solid 3px #282828;
    cursor: pointer;
}


.random_dice {
    background-image: url("./dice.png");
    background-size: 80px 80px;
    background-position: center center;
    background-color: #282828;
    background-repeat: no-repeat;
    width: 120px;
    height: 120px;
    border: solid 5px #ebff00;
    border-radius: 10px;
    cursor: pointer;
  }

.word {
    display: inline;
    padding-top: 90px;
    margin-right: 37%;
    margin-top: 30px;
    margin-bottom: -10px;
    color: #A4A4A4;
    font-size: 35px;
    font-weight: 700;
}

.user_search_result {
    display: flex;
    position: relative;
    text-align: center;
    
    background-color: #282828;
    background: linear-gradient(to right, black, #282828);
    width: 63%;
    height: 140px;
    
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 30px;
    padding-right: 30px;
    
    border: solid 5px white;
    border-radius: 120px;

    color: white;
    font-size: 40px;
    font-weight: 700;
}

.user_UI {
    position: absolute;
    background-color: #282828;
    
    width: 140px;
    height: 140px;
    margin-top: -5px;
    margin-left: -35px;
    margin-right: 50px;

    border-radius: 120px;
    border: 5px solid white;
}

.user_ID {
    position: relative;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 150px;
    font-size: 50px;
}

.user_like {
    position: relative;
    margin-top: auto;
    margin-bottom: auto;
    margin-right: 30px;
}

.s_heart{
    background-image: url("./heart_search.png");
    filter: brightness(0) invert(1) grayscale(1);
    background-size: 40px 40px;
    background-position: center center;
    background-color: none;
    color: white;
    background-repeat: no-repeat;
    width: 120px;
    height: 120px;
    margin-top: 13px;
    margin-right: -25px;
    margin-left: auto;
    border-radius: 10px;

    cursor: pointer;
}